<template>
    <ui-accordion css="styled fluid" v-if="item.owner_id===user_id||item.state==''" >
      <ui-title><ui-icon css="dropdown"></ui-icon>
       <i class="user icon"  v-if="item.state!=''"></i>
      {{item.title}}[{{item.state}}]
      <label v-if="!!user_id">----{{item.owner}}[{{item.ownerMobile}}] <label>
      </ui-title>
      <ui-content>
       <ui-button css="ui primary button"  @click="showMap()"></ui-button>
  <div v-if="item.owner_id===user_id">
      <edit-book :book='item'></edit-book>
      <br>
      <div v-if="item.owner_id===user_id&&''!=item.requesterMobile">
        
        <ui-label>{{item.requester}}[{{item.requesterMobile}}]--{{item.recDate | timeToNow}}</ui-label>
        <br>
        <br>
    <!--  <select v-model="state" class="ui dropdown">
          <option selected></option>
          <option></option>
        </select>-->
         <br>
        <ui-button v-if="item.state===''" css="ui primary button" @click="confirmBook(item,'')"></ui-button>
         <ui-button css="ui primary button" style='margin-left:2em;' @click="confirmBook(item,'')"></ui-button>
      </div>
  </div>
  <div v-else>
       <ui-label>{{item.tags}}</ui-label>
       <br>
       <ui-label>{{item.author}}</ui-label>
       <br>
{{{item.description}}}
       <br>
      <p v-if="!user_id">!</p>
       <br>
      <div  v-if="user_id&&item.state==''">
        <p>:-),!<br>!</p>
       <a href="sms:{{item.ownerMobile}}"></a>
       <a href="mailto:{{owner.email}}" style=" margin-left: 4em;"></a><br><br>
       <ui-button css="ui primary button"  @click="requestBook(item,userName,mobile)"></ui-button>
      </div>
 </div>

    
   
      </ui-content>
    </ui-accordion>
</template>

<script>
import { requestBook,confirmBook } from '../vuex/actions'
import  db  from '../api/db'
import EditBook from '../components/EditBook'
export default {
  components: { EditBook},
  props:{
     item:{
       type: Object,
        default:  ()=>{}
     }
  },
  ready(){
   // console.log("fetch Owner User Info ")
    db.fetchUser(this.item['owner_id'])
       .then((data)=>{
         this.owner=data
       //  console.log(this.owner)
     })
  },
  data(){
    // let ua = navigator.userAgent.toLowerCase();
     return {
       owner:{}
      //isIOS:ua.indexOf("iphone") > -1 || ua.indexOf("ipad") > -1?true:false
      
    }
  },
  
  methods: {
      showMap() {
         this.$dispatch('show-book-on-map', this.item)
      },
   /*   snedSms(){
   
        url=url+"body="+ encodeURIComponent(""+ this.item["title"]+"")
location.href = url

      }*/
  },
  vuex: {
    getters: {
      user_id :({ user }) => user.auth_id,
      userName :({ user }) => user.name,
      mobile :({ user }) => user.mobile,
      email :({ user }) => user.email
    },
    actions: {
      requestBook,confirmBook
    }
  }
}
</script>


